h2 {
  font-size: 26px;
}

h3 {
  font-size: 22px;
}

.invisible {
  visibility: hidden;
}

.cat-info {
  flex-direction: row;
  justify-content: center;
  gap: 50px;
  display: flex;
}

.cat-info p {
  width: 600px;
  font-size: 20px;
}

.loader {
  width: 100px;
  height: 100px;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  border-radius: 50%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 0 0 10px 2px rgba(0, 0, 0, .3);
}

.loader:after {
  animation: rotate 2s linear infinite;
  box-shadow: inset 0 2px #ff3d00;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.centered {
  position: fixed;
  top: 50%;
  left: 50%;
}

select {
  font: inherit;
  box-sizing: border-box;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  background-color: #fff;
  border: thin solid #00f;
  border-radius: 4px;
  margin-bottom: 40px;
  padding: .5em 3.5em .5em 1em;
  line-height: 1.5em;
  display: inline-block;
}

select.breed-select {
  background-image: linear-gradient(45deg, rgba(0, 0, 0, 0) 50%, #00f 50%), linear-gradient(135deg, #00f 50%, rgba(0, 0, 0, 0) 50%), linear-gradient(to right, #87ceeb, #87ceeb);
  background-position: calc(100% - 20px) calc(1em + 2px), calc(100% - 15px) calc(1em + 2px), 100% 0;
  background-repeat: no-repeat;
  background-size: 5px 5px, 5px 5px, 2.5em 2.5em;
}

